import React from "react";
import { useQuery } from "react-apollo";
import gql from "graphql-tag";
import logo from "../assets/logo.png";
import TimeAgo from "../api/TimeAgo";
import ReactMarkdown from "react-markdown";
import Loading from "../components/Loading";

/**
 * Github Api GraphiQL explorer available online:
 * https://developer.github.com/v4/explorer/
 */

const GET_REPOSITORIES = gql`
  query(
    $limit: Int = 10
    $field: String = "CREATED_AT"
    $order: String = "DESC"
  ) {
    viewer {
      repositories(
        first: $limit
        orderBy: { field: $field, direction: $order }
      ) {
        edges {
          node {
            name
            description
            openGraphImageUrl
            url
            updatedAt
          }
        }
      }
    }
  }
`;

const Repositories = () => {
  const { loading, error, data } = useQuery(GET_REPOSITORIES);
  if (loading) return <Loading />;
  if (error) return <h4>error</h4>;
  const {
    viewer: {
      repositories: { edges }
    }
  } = data;

  return (
    <>
      <div>
        {edges.map(edge => (
          <Repository {...edge.node} />
        ))}
      </div>
    </>
  );
};

const Repository = ({
  name,
  description,
  openGraphImageUrl,
  url,
  updatedAt
}) => {
  return (
    <div className="project-container">
      <div className="title">
        <img class="logo" src={logo} alt="woodRock github logo" />
        <span className="project-title-text">
          <span className="header">@woodRock</span>•
          <i className="secondary">{TimeAgo({ date: updatedAt })}</i>
        </span>
        <div className="description text">
          <ReactMarkdown source={description}></ReactMarkdown>
        </div>
      </div>
      <div className="project">
        <a href={url}>
          <img
            class="project-image"
            width="100%"
            height="width"
            src={openGraphImageUrl}
            alt={name}
          />
        </a>
        <div>
          <a href={url} className="github-link">
            <i className="fa fa-github"></i>
          </a>
        </div>
      </div>
    </div>
  );
};

export default Repositories;
